import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import _ from "lodash";
import Input from "./Input";
import formInit from "../utils/formInit";
import calcFinal from "../utils/calcFinal";

const Form = () => {
  const { subjects, init } = formInit(useLocation().pathname);
  const [submitted, setSubmitted] = useState(false);
  const [form, setForm] = useState(init);
  const [result, setResult] = useState(0);

  const onSubmit = (e) => {
    e.preventDefault();
    const totalFactor = _.map(subjects, "factor").reduce(
      (total = 0, num) => total + num
    );
    const final = calcFinal(subjects, form);
    setSubmitted(true);
    setResult(final / totalFactor);
  };

  const onChange = ({ target: { name, value } }) => {
    setForm({ ...form, [name]: value });
  };

  const renderForm = () =>
    subjects.map(({ name, label }) => {
      return (
        <Input
          label={label}
          name={name}
          value={form[name]}
          onChange={onChange}
          key={name}
        />
      );
    });

  const renderResult = () => {
    return <div>{result}</div>;
  };

  return (
    <>
      <form onSubmit={onSubmit}>
        {renderForm()}
        <button type="submit">Submit</button>
      </form>
      {submitted && renderResult()}
    </>
  );
};

export default Form;
